.list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
  }
  .item {
    width: 140px;
  }
  .imgBox {
    width: 140px;
    height: 210px;
    margin-bottom: 10px;
  }
  .img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .title {
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 6px;
  }
  
  .text {
    font-weight: 600;
  }